.customizer-sidebar__header a{
  color: #333;
}
.customizer-sidebar__header a:hover {
  color: #5967dd;
}
.customizer-sidebar__header-title{
  font-size: 16px;
  width: 100%;
  font-weight: 600;
  text-align: center;
}
.customizer-sidebar__header{
  font-size: 20px;
}
.customizer-theme-name{
  font-size: 16px;
}
.customizer-sidebar__block{
  position: relative;
  height: 100%;
  overflow-x: auto;
}
.customizer-sidebar__tab{
  position: absolute;
  width: 75px;
  height: 100%;
  border-right: 1px solid #eff3fb;
}
.customizer-sidebar__tab-content{
  padding-left: 85px;
  padding-right: 5px;
  padding-top: 5px;
}
.customizer-sidebar__tab-block{
  height: 75px;
  border-bottom: 1px solid #eff3fb;
  text-align: center;
  padding-top: 3px;
  overflow: hidden;
}
.customizer-sidebar__tab .active{
  color: #5967dd;
}
.customizer-tab__icon{
  font-size: 26px;
  margin-top: 5px;
  height: 35px;
}
.customizer-tab__title{
  font-size: 12px;
}
.customizer-sidebar__settings-block{
  padding-bottom: 10px;
  padding-top: 3px;
  padding-right: 5px;
}
.customizer-sidebar__settings-block .rangeslider{
  margin-left: 0px;
  margin-right: 0px;
  margin-bottom: 0px;
  margin-top: 5px;
}
.customizer-sidebar__settings-block .rangeslider-horizontal{
  height: 8px;
  background: #ddd;
}
.customizer-sidebar__settings-block .rangeslider-horizontal .rangeslider__handle:after{
