}
.customizer-sidebar__settings-block .rangeslider-horizontal .rangeslider__handle{
  width: 18px;
  height: 18px;
  background: #fff;
  -webkit-box-shadow: 0px 1px 1px 1px rgba(107, 107, 107, 0.35);
  box-shadow: 0px 1px 1px 1px rgba(107, 107, 107, 0.35);
