}
.customizer-sidebar__settings-block .rangeslider-horizontal .rangeslider__fill{
  background: rgba(40, 41, 52, 0.32);
}
.customizer-sidebar__settings-block .rangeslider, .customizer-sidebar__settings-block .rangeslider .rangeslider__fill{
  -webkit-box-shadow: none;
          box-shadow: none;
}
.customizer-sidebar__settings-title{
  padding-bottom: 3px;
}
.customizer-sidebar__color-block{
  width: 100%;
  border: 1px solid #ddd;
  border-radius: 5px;
  padding: 5px;
  cursor: pointer;
}
.customizer-sidebar__color-picker{
  height: 12px;
  border-radius: 2px;
}
.customizer-sidebar__slider-block{
  height: 10px;
  border-radius: 15px;
  background: #ddd;
  position: relative;
  margin-top: 3px;
}
.customizer-sidebar__slider-handle{
  width: 15px;
  height: 15px;
  background: #fff;
  -webkit-box-shadow: 0px 0px 2px 0px #6b6b6b;
          box-shadow: 0px 0px 2px 0px #6b6b6b;
  border-radius: 50%;
  position: absolute;
  top: -3px;
  left: 7px;
  cursor: pointer;
}
.customizer-sidebar__category-body{
  padding: 10px 15px;
}
.customizer-sidebar__category-body .customizer-sidebar__settings-block:last-child{
  padding-bottom: 20px;
}
.customizer-sidebar__category{
  border-bottom: 1px solid #ebedf2;
}
.customizer-sidebar__category-title{
  font-size: 15px;
  padding: 12px 15px;
  font-weight: 500;
  position: relative;
  cursor: pointer;
}
.customizer-sidebar__category-title span{
  margin-right: 5px;
}
.customizer-sidebar__category-title:hover{
  background: rgba(89, 102, 222, 0.04);
}
.customizer-sidebar__category .fa-angle-down, .customizer-sidebar__category .fa-angle-up{
  float: right;
  font-size: 16px;
  margin-top: 3px;
}
.customizer-sidebar__settings-description{
  position: relative;
}
.customizer-sidebar__sketch-picker{
  position: absolute;
  z-index: 3;
  top: 35px;
  left: 0;
}
.customizer-sidebar__setting-background{
  width: 100%;
  border: 1px solid #ddd;
  border-radius: 4px;
  min-height: 60px;
  padding: 5px;
  position: relative;
  text-align: center;
  cursor: pointer;
}
.customizer-sidebar__setting-background img{
  margin: 0 auto;
  max-height: 80px;
  border-radius: 3px;
}
.customizer-sidebar__setting-active {
  background: #f2f3f8;
}
.customizer-sidebar__setting-background-remove{
  position: absolute;
  top: -8px;
  right: -8px;
  text-align: center;
  width: 18px;
  height: 18px;
