  border-radius: 50%;
  box-shadow: 0px 1px 1px 1px rgba(107, 107, 107, 0.35);
  -webkit-box-shadow: 0px 1px 1px 1px rgba(107, 107, 107, 0.35);
  -moz-box-shadow: 0px 1px 1px 1px rgba(107, 107, 107, 0.35);
