}
.customizer-sidebar__setting-background-remove span{
  vertical-align: 2px;
}
.customizer-sidebar__background-upload{
  position: absolute;
  font-size: 12px;
  width: 100%;
  text-transform: uppercase;
  color: #d9ddec;
